<template>
    <div>
        <div class="login_top">
            <van-nav-bar
                title="登录"
                left-arrow
                @click-left="onClickLeft"
            />
        </div>

        <div class="login_main">
            <div class="top">
               
                <van-form @submit="onSubmit">
                    <van-field
                        v-model="username"
                        name="username"
                        label="用户名"
                        placeholder="用户名"
                        :rules="[{ required: true, message: '请填写用户名' }]"
                    />
                    <van-field
                        v-model="password"
                        type="password"
                        name="password"
                        label="密码"
                        placeholder="密码"
                        :rules="[{ required: true, message: '请填写密码' }]"
                    />
                    <div style="margin: 16px;">
                        <van-button round block type="info" native-type="submit">
                        提交
                        </van-button>
                    </div>
                    </van-form>
            </div>
           
            <div class="other">
                <span class="login1">账号登录</span>
                <span class="login2" @click="$router.push('/register')">快速注册</span>
            </div>
            <van-divider>其他登录方式</van-divider>
            <div class="goto">
                <div class="qq">
                    <img src="../../assets/images/qq.png" alt="">
                    <p>QQ</p>
                </div>
                  <div class="wechat">
                    <img src="../../assets/images/wechat.png" alt="">
                    <p>微信</p>
                </div>
            </div>
            <div class="text">
                <p>
                    未注册的手机号验证后将自动创建京东账号, 登录即代表您已同意 <span>京东隐私政策</span>
                    </p>
            </div>

        </div>

  

    </div>
</template>

<script>
import { Toast } from 'vant';
    export default {
         data() {
            return {
            username: '',
            password: '',
            };
        },
        methods: {
            // 返回按钮
            onClickLeft() {
                this.$router.go(-1)
            },
             onSubmit(params) {
                //  定义一个变量存放this的指向
                 let _this = this

                // 判断购物车提交是否有参数传递过来
                let path = "/my"
                if("path" in this.$route.query){
                    path = this.$route.query.path
                }
                // let path = "path" in _this.$route.query ?  _this.$route.query.path : "/my"
                
                // http://127.0.0.1:3000/login?username=zhangsan&password=123123&password2=123123
                console.log(this)
                this.$axios.get("login",{params}).then((res)=>{
                    console.log(res)
                    if(res.data == "200"){
                        Toast({
                            message:"登录成功",
                            onOpened(){
                                _this.$store.state.username = _this.username
                                _this.$router.push(path)
                            }
                        });
                    }else{
                        Toast('登录失败');
                    }
       
                })
   
            },
        }
    }
</script>

<style lang="scss" scoped>
.login_main{
    background-color: white;
    padding: 20px;
    padding-top: 40px;
    margin:  0 auto;
    font-size: 16px;
    .top{
        padding-bottom: 20px;
        border-bottom: 1px solid #f0f0f0;
    }
    
  
    .other{
        color: #b7b7b7;
        font-size: 12px;
        .login2{
           float: right;
        }
    }
    .goto{
        width: 80px;
        height: 60px;
        margin: 0 auto;
        font-size: 12px;
        text-align: center;
        color: #aaabaa;
        img{
            width: 30px;
        }
        .qq{
            float: left;
            margin-right: 20px;
        }
        .wechat{
            float: left;
        }
    }
    .text{
        font-size: 12px;
        color: #aaabaa;
        text-align: center;
        span{
            color: #86a6e3;
        }
    }
   
}
</style>